import React, { Component } from "react";
import axios from "axios";

import SongBlock from "../../components/SongBlock/SongBlock";
import AddSongButton from "../../components/AddSongButton/AddSongButton";
import { styles } from "./SongListStyles";
import { turnIntoLink, getUsername, sortSongs } from "../../util";

export default class SongList extends Component {
	_isMounted = false;
	_shouldRedirect = false;

	constructor(props) {
		super(props);
		this.state = {
			songList: [],
		};
	}

	// Get song list
	componentDidMount() {
		this._isMounted = true;
		this.getSongList();
	}

	// Unmount
	componentWillUnmount() {
		this._isMounted = false;
	}

	// Get song list
	getSongList() {
		getUsername().then((response) => {
			if (this._isMounted) {
				const username = this.props.match.params.username;
				if (response !== username) {
					this._isMounted = false;
					this.props.history.push({
						pathname: "/unauthenticated",
						state: {
							url: !response ? "/login" : `/${response}`,
							message: "This is not your account",
						},
					});
				} else {
					if (this._isMounted) {
						this.getSongs();
					}
				}
			}
		});
	}

	// Fetch songs from backend and sort by recent (higher id = more recent)
	getSongs = () => {
		axios
			.get("https://lyrics-chords.herokuapp.com/api/songs/", {
				withCredentials: true,
			})
			.then((response) => {
				const songs = response.data;
				sortSongs(songs);
				this.setState({ songList: songs });
			})
			.catch(() => {
				alert(
					"There was an error retrieving the songs. Please try again later."
				);
			});
	};

	// Add song handler from ADD SONG button
	addSongHandler = (song) => {
		const list = this.state.songList.slice();
		list.push(song);
		sortSongs(list);
		this.setState({ songList: list });
	};

	// Update song handler from blurring input
	updateSongHandler = (id, name, artist) => {
		const updatedSong = this.state.songList.find((song) => song.id === id);
		updatedSong["name"] = name;
		updatedSong["artist"] = artist;

		const updatedSongList = this.state.songList.filter(
			(song) => song.id !== id
		);
		updatedSongList.push(updatedSong);
		sortSongs(updatedSongList);
		this.setState({ songList: updatedSongList });
	};

	// Delete song handler from trash can
	deleteSongHandler = (id) => {
		const updatedSongList = this.state.songList.filter(
			(song) => song.id !== id
		);
		this.setState({ songList: updatedSongList });
	};

	// Set redirectLink, which is rendered as a Redirect
	redirectHandler = (id) => {
		const item = this.state.songList.find((item) => item.id === id);
		const link = turnIntoLink(item.artist, item.name);
		this.props.history.push(`/${this.props.match.params.username}/${link}`);
	};

	render() {
		return (
			<div style={styles.container}>
				<AddSongButton addSong={this.addSongHandler} />

				<h1 style={styles.title}>Your Songs</h1>

				{this.state.songList.map((item) => (
					<SongBlock
						key={item.id}
						item={item}
						updateSong={this.updateSongHandler}
						deleteSong={this.deleteSongHandler}
						redirectHandler={this.redirectHandler}
						history={this.props.history}
					/>
				))}
			</div>
		);
	}
}
