import React, { useState } from "react";

import "./dropdown.css";
import { styles } from "./DropDownStyles";

export default function DropDown(props) {
	const [menuOpen, setMenuOpen] = useState(false);

	return (
		// To prevent from spanning the entire width
		<div style={{ display: "inline-block" }}>
			<div
				style={styles.container}
				onMouseEnter={() => setMenuOpen(true)}
				onMouseLeave={() => setMenuOpen(false)}
			>
				<div className="button" style={styles.hoverable}>
					Options
				</div>
				{/* Necessary so that position absolute starts at the top left corner of this */}
				<div style={styles.outerDiv}>
					{menuOpen && (
						<div style={styles.options}>
							<p
								className="dropdown-option"
								onClick={props.fetchOnClick}
							>
								Fetch Lyrics
							</p>
							<p
								className="dropdown-option"
								onClick={props.transferOnClick}
							>
								Transfer Lyrics
							</p>
						</div>
					)}
				</div>
			</div>
		</div>
	);
}
