import React, { Component } from "react";
import axios from "axios";
import Cookie from "universal-cookie";

import { getToken, getUsername } from "../../util";
import { styles } from "./LogoutButtonStyles";

const cookie = new Cookie();

export default class LogoutButton extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: null,
		};
	}

	componentDidMount() {
		getUsername().then((response) => {
			this.setState({
				username: response,
			});
		});
	}

	logoutHandler = () => {
		cookie.remove("username");
		axios
			.post(
				"https://lyrics-chords.herokuapp.com/auth/logout",
				{},
				{
					withCredentials: true,
					headers: {
						"X-CSRFToken": getToken(),
					},
				}
			)
			.then(() => this.props.history.push("/login"))
			.catch(() => alert("There was an error logging out."));
	};

	render() {
		let content = (
			<div style={styles.rowContainer}>
				<span>
					You are logged in as&nbsp;&nbsp;
					<span className="emphasis">{this.props.username}</span>
					.&nbsp;
				</span>
				<span onClick={this.logoutHandler} className="navBarLink">
					Logout here.
				</span>
			</div>
		);
		if (!this.props.full) {
			content = (
				<div className="vertical" style={styles.columnContainer}>
					<span className="emphasis" style={styles.name}>
						{this.props.username}
					</span>
					<span onClick={this.logoutHandler} className="navBarLink">
						Logout here.
					</span>
				</div>
			);
		}

		return content;
	}
}
