import axios from "axios";
import React, { Component } from "react";
import CustomSlider from "../../components/CustomSlider/CustomSlider";

import EditableComponent from "../../components/EditableComponent/EditableComponent";
import DropDown from "../../components/DropDown/DropDown";
import LyricsBlock from "../../components/LyricsBlock/LyricsBlock";
import { styles } from "./SongStyles";
import { getToken, turnIntoItem, turnIntoLink, getUsername } from "../../util";

export default class Song extends Component {
	_scrollTimeout = null;
	_scrollSpeed = 3;

	constructor(props) {
		super(props);
		this.state = {
			isRendering: true,
			isFetching: false,
		};
	}

	// Get information about song
	componentDidMount() {
		this.getInfo().then((response) => {
			this.setState(response);
		});
	}

	// Pull information about song from database asynchronously, display Fetching... text in the meantime
	async getInfo() {
		const username = this.props.match.params.username;
		const songName = this.props.match.params.song;

		// Check that the right user is logged in
		const user = await getUsername();
		if (user !== username) {
			this.props.history.push({
				pathname: "/unauthenticated",
				state: {
					url: !user ? "/login" : `/${user}`,
					message: "This is not your account",
				},
			});
			return;
		}

		// Get user's songs
		const response = await axios.get("https://lyrics-chords.herokuapp.com/api/songs/", {
			withCredentials: true,
		});
		const allSongs = response.data;
		const songs = allSongs.filter((song) => {
			const songLink = turnIntoLink(song.artist, song.name);
			return songLink === songName && song.creator === username;
		});

		// If song could not be found
		if (songs.length === 0) {
			this.props.history.push({
				pathname: "/unauthenticated",
				state: {
					url: !user.data ? "/login" : `/${user.data}`,
					message: "The inputted song could not be found",
				},
			});
			return;
		}
		const song = songs[0];

		const stateValues = {
			id: song.id,
			name: song.name,
			artist: song.artist,
			creator: song.creator,
			lyrics: song.lyrics,
			chords: song.chords,
			pulledLyrics: song.pulled_lyrics,
			splitChords: this.getSplitChords(song.chords, song.lyrics),
			strummingPattern: song.strumming_pattern,
			isRendering: false,
		};
		return stateValues;
	}

	// Split chords or set default
	getSplitChords(unsplitChords, lyrics) {
		let chords = unsplitChords.split("~");
		// Default value will produce [""]
		if (chords.length === 1) {
			const splitLyrics = lyrics.split("~");
			let count = 0;
			count +=
				splitLyrics.length -
				splitLyrics.filter((item) => item === "").length;
			chords = [];
			for (let i = 0; i < count; ++i) {
				chords.push("");
			}
		}
		return chords;
	}

	// Update song backend request
	updateSongHelper(choice, value) {
		axios
			.put(
				`https://lyrics-chords.herokuapp.com/api/songs/${this.state.id}/`,
				{
					name: choice === "name" ? value : this.state.name,
					artist: choice === "artist" ? value : this.state.artist,
					lyrics: choice === "lyrics" ? value : this.state.lyrics,
					pulled_lyrics:
						choice === "pulled_lyrics"
							? value
							: this.state.pulledLyrics,
					chords: choice === "chords" ? value : this.state.chords,
					strumming_pattern:
						choice === "strumming_pattern"
							? value
							: this.state.strummingPattern,
				},
				{
					withCredentials: true,
					headers: {
						"Content-Type": "application/json",
						"X-CSRFToken": getToken(),
					},
				}
			)
			.catch(() => {
				alert(
					"There was an error saving your update. Please try again later."
				);
			});
	}

	// Web scrape lyrics
	getLyrics = () => {
		axios
			.get(
				`https://lyrics-chords.herokuapp.com/api/lyrics/${turnIntoLink(
					this.state.artist,
					this.state.name
				)}-lyrics`
			)
			.then((response) => {
				response.data = response.data.trim();
				this.updateSongHelper("pulled_lyrics", response.data);
				this.setState({
					pulledLyrics: response.data,
				});
			})
			.catch(() => {
				this.setState({
					pulledLyrics:
						"There was an error retrieving the lyrics. Please check your spelling and spacing to make sure there isn't a typo. If not, please try again in a few moments.",
				});
			})
			.finally(() => {
				this.setState({ isFetching: false });
			});
	};

	// On lyric transfer, push new lyrics to backend
	transferLyricsHandler = () => {
		const lyrics = this.state.pulledLyrics.replaceAll("\n", "~");
		this.setState({
			lyrics: lyrics,
		});

		this.updateSongHelper("lyrics", lyrics);
		if (!this.state.splitChords) {
			const splitChords = this.getSplitChords(this.state.chords, lyrics);
			this.setState({ splitChords: splitChords });
		}
	};

	// On chords blur, push chord changes to backend
	setSplitChordsHandler = (chords) => {
		this.setState({
			splitChords: chords,
			chords: chords.join("~"),
		});

		this.updateSongHelper("chords", chords.join("~"));
	};

	// On actual lyrics blur, push lyrics to backend
	setSplitLyricsHandler = (lyrics) => {
		this.setState({
			lyrics: lyrics.join("~"),
		});

		this.updateSongHelper("lyrics", lyrics.join("~"));
	};

	// On pulled lyrics blur, push pulled lyrics to backend
	pulledLyricsBlurHandler = (e) => {
		this.updateSongHelper("pulled_lyrics", e.target.innerText);
		this.setState({ pulledLyrics: e.target.innerText });
	};

	// On title or artist blur, update information in backend
	textBlurHandler = (choice, value) => {
		const newName = choice === "name" ? value : this.state.name;
		const newArtist = choice === "artist" ? value : this.state.artist;
		this.setState({
			name: newName,
			artist: newArtist,
		});
		this.props.history.replace(
			`/${this.props.match.params.username}/${turnIntoLink(
				newArtist,
				newName
			)}`
		);
	};

	// On strumming pattern blur, push strumming pattern to backend
	strummingPatternBlurHandler = (e) => {
		this.updateSongHelper("strumming_pattern", e.target.value);
		this.setState({ strummingPattern: e.target.value });
	};

	// When fetching lyrics, make Fetching... text appear before fetching
	setFetching = () => {
		this.setState({ isFetching: true });

		this.getLyrics();
	};

	// Recursive page scroll method
	pageScroll = () => {
		window.scrollBy(0, 1);
		this._scrollTimeout = setTimeout(
			this.pageScroll,
			50 / Math.sqrt(this._scrollSpeed)
		);
	};

	// Clear scroll timeout
	clearTimeoutHandler = () => {
		clearTimeout(this._scrollTimeout);
		window.removeEventListener("mousemove", this.clearTimeoutHandler);
	};

	render() {
		// While rendering, show temporary data
		if (this.state.isRendering) {
			return (
				<div className="horizontal-default">
					<p>Retrieving data...</p>
				</div>
			);
		}

		return (
			<div id="songRoot" style={styles.container}>
				<div className="horizontal">
					<div className="horizontal">
						<DropDown
							fetchOnClick={this.setFetching}
							transferOnClick={this.transferLyricsHandler}
						/>
						{this.state.isFetching && (
							<p style={styles.fetching}>Fetching...</p>
						)}
					</div>
					<div>
						<p
							onClick={() => {
								this.pageScroll();
								window.addEventListener(
									"mousemove",
									this.clearTimeoutHandler
								);
							}}
							className="button"
						>
							Auto-scroll
						</p>
						<CustomSlider
							onChangeCommitted={(_, val) =>
								(this._scrollSpeed = val)
							}
						/>
					</div>
				</div>

				<EditableComponent
					style={styles.title}
					title="name"
					item={turnIntoItem(this.state)}
					content={this.state.name}
					tag="h1"
					onBlur={this.textBlurHandler}
				/>
				<EditableComponent
					style={styles.artist}
					title="artist"
					item={turnIntoItem(this.state)}
					content={this.state.artist}
					tag="h2"
					onBlur={this.textBlurHandler}
				/>

				{/* Strumming pattern */}
				<h2 style={styles.subheading}>Strumming Pattern</h2>
				<input
					style={styles.input}
					defaultValue={this.state.strummingPattern}
					onBlur={this.strummingPatternBlurHandler}
					autoCorrect="off"
				/>

				<div id="lyrics">
					{/* Lyrics */}
					<LyricsBlock
						id="lyrics-block"
						state={this.state}
						setChords={this.setSplitChordsHandler}
						setLyrics={this.setSplitLyricsHandler}
					/>
				</div>

				{/* Pulled lyrics */}
				<h2 style={styles.subheading}>Pulled Lyrics</h2>
				<p
					style={{ ...styles.allowWhiteSpace, ...styles.input }}
					contentEditable={true}
					suppressContentEditableWarning={true}
					onBlur={this.pulledLyricsBlurHandler}
					spellCheck={false}
				>
					{this.state.pulledLyrics
						? this.state.pulledLyrics
						: "Get started by typing the name and artist of a song above. Then, press Options --> Fetch Lyrics to scrape the web for the lyrics of your song or copy paste the lyrics here. When you're satisfied, press Options --> Transfer Lyrics to start annotating the lyrics with chords in the Lyrics section. Once you've annotated, press auto scroll in the top right corner to start playing through!\n\nClick the help section in the top navigation bar to learn more!"}
				</p>
			</div>
		);
	}
}
