import React, { Component } from "react";
import Sidebar from "react-sidebar";

import NavContent from "../NavContent/NavContent";
import { styles } from "./CustomSidebarStyles";
import { withRouter } from "react-router-dom";

class CustomSidebar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			sidebarOpen: true,
		};
	}

	openSidebar = () => {
		this.setState({ sidebarOpen: !this.state.sidebarOpen });
	};

	render() {
		const content = (
			<NavContent
				history={this.props.history}
				style={styles.navContent}
				direction="column"
			/>
		);

		return (
			<Sidebar
				sidebar={content}
				open={this.props.menuOpen}
				onSetOpen={this.props.changeSidebar}
				styles={styles}
			>
				<div>
					{this.props.children}
				</div>
			</Sidebar>
		);
	}
}

export default withRouter(CustomSidebar);
