import React, { Component } from "react";
import { Link } from "react-router-dom";

import { styles } from "./NavContentStyles";
import { getUsername } from "../../util";
import LogoutButton from "../LogoutButton/LogoutButton";

export default class NavContent extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: null,
		};
	}

	componentDidMount() {
		this.getUser();
		this.unlisten = this.props.history.listen(() => {
			this.getUser();
		});
	}

	componentWillUnmount() {
		this.unlisten();
	}

	getUser = () => {
		getUsername().then((response) => {
			const username = !response ? null : response;
			this.setState({ username: username });
		});
	};

	render() {
		return (
			<div
				style={{
					...styles.container,
					flexDirection: this.props.direction,
					paddingBottom: this.props.direction === "column" ? 20 : 0,
				}}
			>
				<div
					style={{
						...this.props.style,
						...styles.leftContainer,
						flexDirection: this.props.direction,
						width:
							this.props.direction === "column" ? "100%" : "auto",
					}}
				>
					<Link
						to="/"
						className="navBarLink navBarHover"
						style={{
							...styles.link,
							width:
								this.props.direction === "column"
									? "100%"
									: "auto",
						}}
					>
						Home
					</Link>
					{this.state.username && (
						<Link
							to={`/${this.state.username}`}
							className="navBarLink navBarHover"
							style={{
								...styles.link,
								width:
									this.props.direction === "column"
										? "100%"
										: "auto",
							}}
						>
							Song List
						</Link>
					)}
					{this.state.username && (
						<Link
							to="/help"
							className="navBarLink navBarHover"
							style={{
								...styles.link,
								width:
									this.props.direction === "column"
										? "100%"
										: "auto",
							}}
						>
							Help
						</Link>
					)}
				</div>
				<div>
					{this.state.username ? (
						<LogoutButton
							history={this.props.history}
							full={this.props.direction === "row"}
							username={this.state.username}
						/>
					) : (
						<Link
							to="/login"
							className="navBarLink"
							style={
								this.props.direction === "row"
									? styles.loginRow
									: styles.loginColumn
							}
						>
							Login
						</Link>
					)}
				</div>
			</div>
		);
	}
}
