import React, { Component } from "react";
import $ from "jquery";

import { styles } from "./LyricsBlockStyles";

export default class LyricsBlock extends Component {
	constructor(props) {
		super(props);
		this.state = {
			splitChords: props.state.splitChords,
		};
	}

	// On chords focus, override default events for Enter, period, comma
	chordsFocusHandler = (idx, event) => {
		$(`#${idx}`).on("keypress", (e) => {
			const chordsCopy = this.state.splitChords.slice();
			if (e.key === ".") {
				e.preventDefault();
				chordsCopy[idx] += "    ";
			} else if (e.key === ",") {
				e.preventDefault();
				chordsCopy[idx] =
					chordsCopy[idx].length <= 4
						? ""
						: chordsCopy[idx].substr(0, chordsCopy[idx].length - 4);
			} else if (e.key === "Enter") {
				e.preventDefault();
				event.target.blur();
				return;
			} else {
				return;
			}
			this.setState({ splitChords: chordsCopy });
		});
	};

	// On chords blur, set chords and remove event listeners
	chordsBlurHandler = (idx) => {
		$(`#${idx}`).off("keypress");
		this.props.setChords(this.state.splitChords);
	};

	// On lyrics focus, override default event for Enter
	lyricsFocusHandler = () => {
		$(".preventEnter").on("keypress", (e) => {
			if (e.key === "Enter") {
				e.preventDefault();
				e.target.blur();
			}
		});
	};

	// On lyrics blur, remove event listener for Enter
	lyricsBlurHandler = (idx, event) => {
		$(".preventEnter").off("keypress");

		const lyricsCopy = this.props.state.lyrics.split("~").slice();
		lyricsCopy[idx] = event.target.innerText;

		this.props.setLyrics(lyricsCopy);
	};

	// Takes in string lyrics and turns each into a string and input div
	renderItems = () => {
		return this.props.state.lyrics.split("~").map((item, idx) => {
			// At the end of each section, add an extra space
			if (item === "") {
				return (
					<div key={idx}>
						<br />
						<br />
					</div>
				);
			}

			return (
				<div key={idx}>
					<input
						style={styles.input}
						id={idx}
						onFocus={this.chordsFocusHandler.bind(this, idx)}
						onBlur={this.chordsBlurHandler.bind(this, idx)}
						value={this.state.splitChords[idx]}
						onChange={(e) => {
							let chordsCopy = this.state.splitChords.slice();
							chordsCopy[idx] = e.target.value;
							this.setState({ splitChords: chordsCopy });
						}}
						autoCorrect="off"
					/>
					<p
						className="preventEnter"
						style={{
							...styles.lyricLine,
							...styles.allowWhiteSpace,
						}}
						contentEditable={true}
						suppressContentEditableWarning={true}
						onFocus={() => this.lyricsFocusHandler()}
						onBlur={this.lyricsBlurHandler.bind(this, idx)}
						spellCheck={false}
					>
						{item}
					</p>
				</div>
			);
		});
	};

	render() {
		return (
			<div>
				<h2 style={styles.lyricsTitle}>Lyrics</h2>
				{this.props.state.lyrics === "" ? (
					<p style={styles.lyricLine}>No lyrics found.</p>
				) : (
					this.renderItems()
				)}
			</div>
		);
	}
}
