import React, { Component } from "react";

export default class RedirectScreen extends Component {
	componentDidMount() {
		setTimeout(() => {
			this.props.history.push(
				this.props.location.state ? this.props.location.state.url : "/login"
			);
		}, 2000);
	}

	render() {
		return (
			<div className="horizontal-default">
				<h2>{this.props.location.state && this.props.location.state.message}. Redirecting...</h2>
			</div>
		);
	}
}
