import React from "react";
import axios from "axios";

import EditableComponent from "../EditableComponent/EditableComponent";
import { styles } from "./SongBlockStyles";
import { getToken } from "../../util";
import trashCan from "../../constants/images/image-removebg-preview.png";

export default function SongBlock(props) {
	// Delete song request
	function deleteHandler() {
		axios
			.delete(`https://lyrics-chords.herokuapp.com/api/songs/${props.item.id}/`, {
				withCredentials: true,
				headers: {
					"X-CSRFToken": getToken(),
				},
			})
			.then(props.deleteSong(props.item.id));
	}

	return (
		<div style={styles.songContainer}>
			<div style={styles.horizontal}>
				<img
					alt="Delete"
					src={trashCan}
					style={styles.trashCan}
					onClick={deleteHandler}
				/>
				<div>
					<EditableComponent
						title="name"
						style={styles.name}
						item={props.item}
						content={props.item.name}
						tag="h2"
						updateSong={props.updateSong}
					/>
					<EditableComponent
						title="artist"
						style={styles.artist}
						item={props.item}
						content={props.item.artist}
						tag="p"
						updateSong={props.updateSong}
					/>
				</div>
			</div>
			<div
				style={styles.imageContainer}
				onClick={props.redirectHandler.bind(this, props.item.id)}
			>
				<img
					src="https://i.pinimg.com/originals/58/1d/34/581d34b9daddc9f6eec84accc93c7a0c.png"
					style={styles.arrow}
					alt="Arrow"
				/>
			</div>
		</div>
	);
}
