import React from "react";
import axios from "axios";

import { getToken, getUsername } from "../../util";

export default function AddSongButton(props) {
	// Add new song to backend
	function addSongHandler() {
		getUsername().then((username) => {
			axios
				.post(
					"https://lyrics-chords.herokuapp.com/api/songs/",
					{
						name: "Click on me to turn me into a song name",
						artist: "Turn me into an artist",
						creator: username,
					},
					{
						withCredentials: true,
						headers: {
							"X-CSRFToken": getToken(),
						},
					}
				)
				.then((response) => {
					const song = {
						id: response.data.id,
						name: "Click on me to turn me into a song name",
						artist: "Turn me into an artist",
						creator: username,
						lyrics: "",
						pulled_lyrics: "",
						chords: "",
						strumming_pattern: "",
					};
					props.addSong(song);
				})
				.catch(() =>
					alert(
						"There was an error adding a new songs. Please try again later."
					)
				);
		});
	}

	return (
		<span className="bold" onClick={addSongHandler}>
			ADD SONG
		</span>
	);
}
