import axios from "axios";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import Cookie from "universal-cookie";

import { styles } from "./AuthStyles";
import { getToken } from "../../util";

const cookie = new Cookie();

export default function Auth(props) {
	const data = props.location.state;

	const [username, setUsername] = useState(data ? data.username : "");
	const [password, setPassword] = useState(data ? data.password : "");
	const [login, setLogin] = useState(data ? data.login : true);

	// Submit handler
	async function submitHandler(event) {
		axios
			.get("https://lyrics-chords.herokuapp.com/auth/csrf", {
				withCredentials: true,
			})
			.catch(() =>
				alert(
					"There was an error getting your CSRF token. Please report this to me at sam4button@gmail.com."
				)
			);

		event.preventDefault();
		const state = login ? "login" : "signup";

		try {
			const response = await axios.post(
				`https://lyrics-chords.herokuapp.com/auth/${state}`,
				{
					username: username,
					password: password,
				},
				{
					withCredentials: true,
					headers: {
						"X-CSRFToken": getToken(),
					},
				}
			);
			// .then((response) => {
			// Login successful or signup successful respectively
			if (
				response.data.indexOf("successful") > -1 ||
				response.data.indexOf("Your username is") > -1
			) {
				props.history.push(`/${username}`);
				cookie.set("username", username, { path: "/" });
			} else {
				alert(response.data);
			}
			// })
			// .catch(() =>
			// 	alert("There was an error handling your credentials.")
			// );
		} catch (err) {
			alert("There was an error handling your credentials.");
		}
	}

	if (window.location.href.indexOf("signup") > -1 && login) {
		setLogin(false);
	} else if (window.location.href.indexOf("login") > -1 && !login) {
		setLogin(true);
	}

	return (
		<div style={styles.container}>
			<div>
				<h1 style={styles.header}>{login ? "Login" : "Sign Up"}</h1>
				<form>
					{/* Username */}
					<label style={styles.label} htmlFor="username">
						Username
					</label>
					<br />
					<input
						id="username"
						style={styles.field}
						value={username}
						onChange={(e) => setUsername(e.target.value)}
					/>
					<br />

					{/* Password */}
					<label style={styles.label} htmlFor="password">
						Password
					</label>
					<br />
					<input
						type="password"
						id="password"
						style={styles.field}
						value={password}
						onChange={(e) => setPassword(e.target.value)}
					/>
					<br />

					{/* Buttons */}
					<div style={styles.buttonContainer}>
						<button className="button" onClick={submitHandler}>
							Submit
						</button>
						<Link
							to={login ? "/signup" : "/login"}
							className="button"
						>
							Switch to {login ? "Sign up" : "Login"}
						</Link>
					</div>
				</form>
			</div>
		</div>
	);
}
