import React, { Component } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import NavBar from "./components/NavBar/NavBar";
import SongList from "./screens/SongList/SongList";
import Song from "./screens/Song/Song";
import Error from "./components/Error/Error";
import Auth from "./screens/Auth/Auth";
import Help from "./screens/Help/Help";
import Home from "./screens/Home/Home";
import RedirectScreen from "./components/RedirectScreen/RedirectScreen";
import CustomSidebar from "./components/CustomSideBar/CustomSidebar";
import MenuBar from "./components/MenuBar/MenuBar";

export default class App extends Component {
	constructor() {
		super();
		this.state = {
			menuOpen: false,
			orientation: window.matchMedia("(orientation: landscape)").matches,
		};

		window
			.matchMedia("(orientation: landscape)")
			.addEventListener("change", () => {
				this.setState({
					orientation: window.matchMedia("(orientation: landscape)")
						.matches,
				});
			});
	}

	menuClickHandler = () => {
		this.setState({ menuOpen: !this.state.menuOpen });
	};

	render() {
		return (
			<BrowserRouter>
				{this.state.orientation ? (
					<div>
						<NavBar />
						<Switch>
							<Route path="/sidebar" component={CustomSidebar} />
							<Route exact path="/" component={Home} />
							<Route
								path={["/login", "/signup"]}
								component={Auth}
							/>
							<Route path="/help" component={Help} />
							<Route
								path="/unauthenticated"
								component={RedirectScreen}
							/>
							<Route
								exact
								path="/:username"
								component={SongList}
							/>
							<Route path="/:username/:song" component={Song} />
							<Route component={Error} />
						</Switch>
					</div>
				) : (
					<div>
						<CustomSidebar
							menuOpen={this.state.menuOpen}
							changeSidebar={this.menuClickHandler}
						>
							<MenuBar onMenuClick={this.menuClickHandler} />
							<Switch>
								<Route
									path="/sidebar"
									component={CustomSidebar}
								/>
								<Route exact path="/" component={Home} />
								<Route
									path={["/login", "/signup"]}
									component={Auth}
								/>
								<Route path="/help" component={Help} />
								<Route
									path="/unauthenticated"
									component={RedirectScreen}
								/>
								<Route
									exact
									path="/:username"
									component={SongList}
								/>
								<Route
									path="/:username/:song"
									component={Song}
								/>
								<Route component={Error} />
							</Switch>
						</CustomSidebar>
					</div>
				)}
			</BrowserRouter>
		);
	}
}
