import axios from "axios";
import Cookie from "universal-cookie";

const cookie = new Cookie();

// Get CSRF token
export function getToken() {
	let cookieValue = null;
	const name = "csrftoken";

	if (document.cookie && document.cookie !== "") {
		const cookies = document.cookie.split(";");
		for (var i = 0; i < cookies.length; i++) {
			let cookie = cookies[i].trim();
			if (cookie.substring(0, name.length + 1) === name + "=") {
				cookieValue = decodeURIComponent(
					cookie.substring(name.length + 1)
				);
				break;
			}
		}
	}
	return cookieValue;
}

// Get username
export async function getUsername() {
	try {
		const existingUsername = cookie.get("username");
		if (existingUsername) {
			return existingUsername;
		}
		const response = await axios.get("https://lyrics-chords.herokuapp.com/auth/user", {
			withCredentials: true,
		});
		cookie.set("username", response.data, { path: "/" });
		return response.data;
	} catch (err) {
		alert(
			"There was an error determining your username. Please check that you are connected to a network."
		);
		return "Error";
	}
}

// Turn artist and name into link
export function turnIntoLink(artist, name) {
	return `${artist.replaceAll(" ", "-").toLowerCase()}-${name
		.replaceAll(" ", "-")
		.toLowerCase()}`;
}

// Turn details into item to pass into EdtiableComponent from Song.js
export function turnIntoItem(state) {
	return {
		artist: state.artist,
		chords: state.chords,
		creator: state.creator,
		id: state.id,
		lyrics: state.lyrics,
		name: state.name,
	};
}

// Sort songs by order of creation (id)
export function sortSongs(list) {
	list.sort((a, b) => a.id < b.id);
}
