import React from "react";
import axios from "axios";
import $ from "jquery";

import { getToken } from "../../util";

export default function EditableComponent(props) {
	// Blurs input on Enter key
	function textFocusHandler(event) {
		$(".preventEnter").on("keypress", (e) => {
			if (e.key === "Enter") {
				e.preventDefault();
				event.target.blur();
			}
		});
	}

	// Update song request
	function textBlurHandler(event) {
		$(".preventEnter").off("keypress");
		const val = event.target.textContent;

		const choice = props.title;
		const id = props.item.id;
		const name = props.item.name;
		const artist = props.item.artist;
		const creator = props.item.creator;

		const newName = choice === "name" ? val : name;
		const newArtist = choice === "artist" ? val : artist;

		// For Song - update state
		if (props.onBlur) {
			props.onBlur(choice, val);
		}

		axios
			.put(
				`https://lyrics-chords.herokuapp.com/api/songs/${id}/`,
				{
					name: newName,
					artist: newArtist,
					creator: creator,
				},
				{
					withCredentials: true,
					headers: {
						"Content-Type": "application/json",
						"X-CSRFToken": getToken(),
					},
				}
			)
			.then(() => {
				// For SongList
				if (props.updateSong) {
					props.updateSong(id, newName, newArtist);
				}
			})
			.catch(() => {
				alert("There was an error updating the song.");
			});
	}

	const Tag = props.tag;
	const { style } = props;

	return (
		<Tag
			style={{ ...style }}
			className="preventEnter"
			contentEditable={true}
			suppressContentEditableWarning={true}
			onBlur={textBlurHandler}
			onFocus={textFocusHandler}
		>
			{props.content}
		</Tag>
	);
}
